"use client";
import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";
import { getAnalytics } from "firebase/analytics";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import { app, db } from "@/app/api/firebase";
import { collection, query, where, getDocs } from "firebase/firestore";
import Sidebar from "@/app/components/sidebar";
import Navbar from "@/app/components/navbar";


export default function Home() {
    const auth = getAuth(app);
    const analytics = getAnalytics(app);
    const [projects, setProjects] = useState([]);
    const [meetings, setMeetings] = useState([]);
    const [userName, setUserName] = useState("");
    const [formData, setFormData] = useState({
    title: '',
    description: '',
    status: '',
    owner: '',
    participants: []
  });
    

        onAuthStateChanged(auth, (user) => {
            if (user) {
                fetchUserData(user);
            }
        });
 

        const fetchProjects = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, 'projects'));
      const list = [];
      querySnapshot.forEach(d => list.push({ id: d.id, ...d.data() }));
      setProjects(list);
    } catch (error) {
      console.error('Error fetching projects:', error);
    } finally {
      setLoading(false);
    }
  };
  useEffect(() => {
    fetchProjects();
    // also fetch users for owner dropdown
    (async function fetchUsers() {
      try {
        const q = await getDocs(collection(db, 'users'));
        const u = [];
        q.forEach(d => u.push({ id: d.id, ...d.data() }));
        setUsersList(u);
      } catch (err) {
        console.error('Error fetching users for owner dropdown:', err);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

    const formatDate = (date) => {
        return new Date(date).toLocaleDateString('en-GB', {
            day: '2-digit',
            month: '2-digit',
            year: 'numeric'
        });
    };

    const today = new Date().toLocaleDateString('en-GB', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
        weekday: 'long'
    });

    return (
        <div className="flex">
            <Sidebar/>
            <div className="flex-1">
                <Navbar/>
                <div className="p-6">
                    <div className="bg-gray-50 rounded-xl p-6 mb-6">
                        <h1 className="text-3xl font-bold mb-2">Hello {userName}</h1>
                        <div className="text-gray-600">
                            <h2 className="text-xl">Today</h2>
                            <p className="text-2xl font-semibold">{today}</p>
                        </div>
                    </div>

                    <div className="mb-8">
                        <h2 className="text-xl font-semibold mb-4">Your Projects:</h2>
                        <div className="flex gap-4 flex-wrap">
                            
                        </div>
                    </div>

                    <div className="bg-blue-50 rounded-xl p-6">
                        <h2 className="text-xl font-semibold mb-4">Meetings</h2>
                        <div className="space-y-4">
                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
